import React, { Component } from "react";
import { Row, Col } from "reactstrap";
import ReactDOM from "react-dom";
import slide1 from "../img/pump/slide1.png";
import slide2 from "../img/pump/slide2.png";
import slide3 from "../img/pump/slide3.png";
import slide4 from "../img/pump/slide4.png";
import slide5 from "../img/pump/slide5.png";
import slide6 from "../img/pump/slide6.png";
import { TweenLite, TimelineLite } from "gsap";
import { ScrollToPlugin } from "gsap/all";
import Swiper from "react-id-swiper";
const timing = 1;
export default class Pump extends Component {
  constructor(props) {
    super(props);
    this.pumpSwiper = null;
  }

  componentDidMount() {
    // eslint-disable-next-line
    const plugins = [ScrollToPlugin];
    this.pageSwiper = ReactDOM.findDOMNode(
      this
    ).parentElement.parentElement.parentElement.swiper;

    !this.props.isSmallScreen && this.props.disableMousewheel(this.pageSwiper);
    this.pumpSwiper = ReactDOM.findDOMNode(this).getElementsByClassName(
      "swiper-container"
    )[0].swiper;
    if (this.pumpSwiper !== null) {
      this.props.setActiveSubpageIndex(this.pumpSwiper, 2);
    }
  }
  componentWillUnmount() {
    let pageSwiper = ReactDOM.findDOMNode(this).parentElement.parentElement
      .parentElement.swiper;
    this.pumpSwiper = null;
    !this.props.isSmallScreen && this.props.enableMousewheel(pageSwiper);
  }
  render() {
    const params = {
      autoHeight: true,
      slideActiveClass: "swiper-slide-active pump-slide-active",
      loop: true,
      mousewheel: true,
      speed: timing * 1000,
      effect: "fade",
      fadeEffect: {
        crossFade: true
      },
      breakpointsInverse: true,
      breakpoints: {
        1200: {
          autoHeight: false
        }
      },
      pagination: {
        el:
          ".swiper-pagination.customized-swiper-pagination.swiper-pagination-nested",
        clickable: true
      },
      on: {
        init: () => {
          this.pumpSwiper = ReactDOM.findDOMNode(this).swiper;
        },
        transitionEnd: () => {
          TweenLite.to(window, 1, {
            scrollTo: {
              y: `#${this.props.dataHistorySubmenu.slug}`,
              offsetY: 110
            }
          });
        },
        slideNextTransitionStart: () => {
          this.activeSection = ReactDOM.findDOMNode(
            this
          ).getElementsByClassName("pump-slide-active")[0];
          this.header = this.activeSection.getElementsByClassName("heading")[0];
          this.bar1 = this.header.getElementsByClassName("bar-1")[0];

          this.leftAnimation = this.activeSection.getElementsByClassName(
            "left-animation"
          )[0];
          this.rightAnimation = this.activeSection.getElementsByClassName(
            "right-animation"
          )[0];

          if (
            this.leftAnimation !== undefined ||
            this.rightAnimation !== undefined
          ) {
            let tl = new TimelineLite();
            tl.to(this.bar1, timing / 2, { width: "110%" })

              .fromTo(
                this.leftAnimation,
                timing,
                { y: 100, opacity: 0 },
                { y: 0, opacity: 1 },
                `-=${timing}`
              )

              .fromTo(
                this.rightAnimation,
                timing,
                { x: 100, opacity: 0 },
                { x: 0, opacity: 1 },
                `-=${timing}`
              )
              .to(this.bar1, timing / 3, { width: "0%" });
          }
        },
        slidePrevTransitionStart: () => {
          this.activeSection = ReactDOM.findDOMNode(
            this
          ).getElementsByClassName("pump-slide-active")[0];
          this.header = this.activeSection.getElementsByClassName("heading")[0];

          this.bar1 = this.header.getElementsByClassName("bar-1")[0];

          this.leftAnimation = this.activeSection.getElementsByClassName(
            "left-animation"
          )[0];
          this.rightAnimation = this.activeSection.getElementsByClassName(
            "right-animation"
          )[0];

          if (
            this.leftAnimation !== undefined ||
            this.rightAnimation !== undefined
          ) {
            let tl = new TimelineLite();
            tl.to(this.bar1, timing / 2, { width: "110%" })

              // .fromTo(
              //   this.header,
              //   timing,
              //   { x: -100, opacity: 0 },
              //   { x: 0, opacity: 1 }
              // )
              .fromTo(
                this.leftAnimation,
                timing,
                { y: -100, opacity: 0 },
                { y: 0, opacity: 1 },
                `-=${timing}`
              );

            if (this.rightAnimation !== undefined) {
              tl.fromTo(
                this.rightAnimation,
                timing,
                { x: -100, opacity: 0 },
                { x: 0, opacity: 1 },
                `-=${timing}`
              );
            }
            tl.to(this.bar1, timing / 3, { width: "0%" });
          }
        },
        reachEnd: () => {
          !this.props.isSmallScreen &&
            this.props.enableMousewheel(this.pageSwiper);
        },
        reachBeginning: () => {
          !this.props.isSmallScreen &&
            this.props.enableMousewheel(this.pageSwiper);
        }
      }
    };

    return (
      <div className="content-wrapper" id={this.props.dataHistorySubmenu.slug}>
        <Swiper {...params}>
          <div>
            <Row noGutters className="slide-1">
              <Col xs={12} md={6}>
                <h2 className="heading">
                  <div className="bar-1" />
                  Pompy ciepa powietrze-woda
                </h2>
                <div className="left-animation">
                  <h3 className="subheading">Co zyskujemy?</h3>
                  <ul>
                    <li>
                      <span>Przyjazne </span>rodowisku ogrzewanie bez oleju,
                      gazu czy wgla.
                    </li>
                    <li>
                      <span>Bezobsugowo</span> systemu grzewczego - pompa
                      ciepa pracuje cakowicie automatycznie. Nie wymaga
                      zewntrznej ingerencji w postaci uzupeniania paliwa. Nie
                      trzeba si troszczy o termin wizyty kominiarza czy
                      serwisanta kota.
                    </li>
                    <li>
                      <span>Oszczdno</span> miejsca  nie trzeba wydziela
                      pomieszczenia kotowni, modu wewntrzny pompy ciepa moe
                      by umieszczony np. w kuchni.
                    </li>
                    <li>
                      <span>Nisze</span> koszty inwestycyjne w przypadku budowy
                      nowego domu  nie trzeba budowa komina.
                    </li>
                    <li>
                      <span>Nie</span> wytwarza pomieni co za tym idzie nie
                      stanowi zagroenia poarowego.
                    </li>
                  </ul>
                </div>
              </Col>
              <Col xs={12} md={6} className="right-animation align-self-center">
                <div
                  className="image-wrapper"
                  style={{
                    backgroundImage: `url(${slide1})`,
                    backgroundPosition: "center center",
                    backgroundSize: "contain",
                    backgroundRepeat: "no-repeat"
                  }}
                />
              </Col>
            </Row>
          </div>
          <div>
            <Row noGutters className="slide-2">
              <Col xs={12} md={6}>
                <h2 className="heading">
                  <div className="bar-1" />
                  Pompy ciepa powietrze-woda
                </h2>
                <div className="left-animation">
                  <h3 className="subheading">Zasada dziaania</h3>
                  <p className="paragraph">
                    Pompa ciepa dostarczy ciepo zarwno na potrzeby
                    centralnego ogrzewania, jak i ciepej wody uytkowej.
                    Urzdzenie do pracy wykorzystuje ten sam ukad chodniczy,
                    co potocznie zwany klimatyzator.
                  </p>
                </div>
              </Col>
              <Col xs={12} md={6} className="right-animation align-self-center">
                <div
                  className="image-wrapper"
                  style={{
                    backgroundImage: `url(${slide2})`,
                    backgroundPosition: "center center",
                    backgroundSize: "contain",
                    backgroundRepeat: "no-repeat"
                  }}
                />
              </Col>
            </Row>
          </div>
          <div>
            <Row noGutters className="slide-3">
              <Col xs={12} md={6}>
                <h2 className="heading">
                  <div className="bar-1" />
                  Pompy ciepa powietrze-woda
                </h2>
                <div className="left-animation">
                  <div className="left-animation" />
                  <h3 className="subheading">Typy pomp ciepa</h3>
                  <ul>
                    <li>
                      <span className="font-weight-800 text-red">
                        POMPA CIEPA TYPU SPLIT
                      </span>{" "}
                      - poszczeglne komponenty rozdzielone s na dwie
                      jednostki, ktre poczone s ze sob ukadem chodniczym.
                      W jednostce zewntrznej <strong>(1) </strong>
                      znajduje si parownik <strong>(4)</strong> w formie
                      lamelowego wymiennika, wentylator, sprarka{" "}
                      <strong>(3)</strong>, zawr 4-drogowy <strong>(6)</strong>{" "}
                      oraz zawr rozpry <strong>(5)</strong>. Natomiast w
                      jednostce wewntrznej <strong>(2) </strong>
                      umieszczony jest parownik <strong>(7)</strong> w formie
                      pytowego wymiennika, pompa obiegowa <strong>(8)</strong>,
                      grzaka elektryczna <strong>(9)</strong> oraz zawr
                      trjdrogowy <strong>(10)</strong>.
                    </li>
                  </ul>
                </div>
              </Col>
              <Col xs={12} md={6} className="right-animation align-self-center">
                <div
                  className="image-wrapper"
                  style={{
                    backgroundImage: `url(${slide3})`,
                    backgroundPosition: "center center",
                    backgroundSize: "contain",
                    backgroundRepeat: "no-repeat"
                  }}
                />
              </Col>
            </Row>
          </div>
          <div>
            <Row noGutters className="slide-4">
              <Col xs={12} md={6}>
                <h2 className="heading">
                  <div className="bar-1" />
                  Pompy ciepa powietrze-woda
                </h2>
                <div className="left-animation">
                  <h3 className="subheading">Typy pomp ciepa</h3>
                  <ul>
                    <li>
                      <span className="font-weight-800 text-red">
                        POMPA CIEPA TYPU MONOBLOK
                      </span>{" "}
                      - wszystkie elementy ukadu chodniczego, takie jak:
                      sprarka <strong>(2)</strong>, parownik{" "}
                      <strong>(3)</strong>, zawr rozprny <strong>(4)</strong>
                      , zawr 4-drogowy <strong>(5)</strong> i skraplacz{" "}
                      <strong>(6)</strong>, umieszczone s w jednostce
                      zewntrznej <strong>(1)</strong>. Urzdzenie z instalacj
                      grzewcz poczone jest w sposb hydrauliczny, gdzie
                      czynnikiem roboczym jest roztwr glikolowy.
                    </li>
                  </ul>
                </div>
              </Col>
              <Col xs={12} md={6} className="right-animation align-self-center">
                <div
                  className="image-wrapper"
                  style={{
                    backgroundImage: `url(${slide4})`,
                    backgroundPosition: "center center",
                    backgroundSize: "contain",
                    backgroundRepeat: "no-repeat"
                  }}
                />
              </Col>
            </Row>
          </div>
          <div>
            <Row noGutters className="slide-5">
              <Col xs={12} md={6}>
                <h2 className="heading">
                  <div className="bar-1" />
                  Pompy ciepa powietrze-woda
                </h2>
                <div className="left-animation">
                  <h3 className="subheading">COP - co to takiego?</h3>
                  <ul>
                    <li>
                      <span className="font-weight-800 text-red">COP</span>{" "}
                      <span className="font-weight-800 text-red">
                         z j. ang Coefficient of Performance
                      </span>
                      , w wolnym tumaczeniu {" "}
                      <strong>wspczynnik wydajnoci</strong>. Jest to stosunek
                      mocy grzewczej do energii elektrycznej, pobranej przez
                      urzdzenie do pracy, w okrelonych przez producenta
                      warunkach temperaturowych. Producenci pomp ciepa typu
                      powietrze/woda s zobowizani, aby dla swoich produktw
                      podawa warto COP dla parametrw A7W35 i A2W35. Oznacza
                      to, e podawane COP dotyczy temperatury powietrza
                      zewntrznego <strong>(A)</strong> na poziomie 7 lub 2
                      stopni Celsjusza, a temperatura wody w instalacji
                      grzewczej <strong>(W)</strong> 35 stopni Celsjusza.
                    </li>
                  </ul>
                  <p className="paragraph">
                    <strong>Przykad: </strong>Gdy producent podaje, e
                    wspczynnik COP dla danego urzdzenia wynosi 5 oznacza to,
                    e dla budynku, w ktrym zapotrzebowanie na ciepo wynosi
                    okoo 10 kW, 8 kW pokryte jest z tzw. energii odnawialnej, a
                    2 kW z energii elektrycznej.
                  </p>
                </div>
              </Col>
              <Col xs={12} md={6} className="right-animation align-self-center">
                <div
                  className="image-wrapper"
                  style={{
                    backgroundImage: `url(${slide5})`,
                    backgroundPosition: "center center",
                    backgroundSize: "contain",
                    backgroundRepeat: "no-repeat"
                  }}
                />
              </Col>
            </Row>
          </div>
          <div>
            <Row noGutters className="slide-6">
              <Col xs={12} md={6}>
                <h2 className="heading">
                  <div className="bar-1" />
                  Pompy ciepa powietrze-woda
                </h2>
                <div className="left-animation">
                  <h3 className="subheading">
                    Jaka instalacja c.o. do pompy ciepa?
                  </h3>

                  <p className="paragraph">
                    Pompa ciepa to urzdzenie, ktre z zaoenia ma pracowa
                    energooszczdnie. Krtko mwic, im nisz temperatur
                    musimy zada wodzie krcej w instalacji, tym pompa ciepa
                    bdzie pracowa efektywniej. Biorc pod uwag dzisiejsze
                    trendy w dziedzinie instalacji c.o., czynnik o najniej
                    temperaturze prawie zawsze wykorzystywany jest w przypadku
                    ogrzewania podogowego. Taki typ instalacji zastosowany w
                    caym domu jest najbardziej ekonomicznym rozwizaniem z
                    eksploatacyjnego punktu widzenia.
                  </p>
                </div>
              </Col>
              <Col xs={12} md={6} className="right-animation align-self-center">
                <div
                  className="image-wrapper"
                  style={{
                    backgroundImage: `url(${slide6})`,
                    backgroundPosition: "center center",
                    backgroundSize: "contain",
                    backgroundRepeat: "no-repeat"
                  }}
                />
              </Col>
            </Row>
          </div>
        </Swiper>

        {this.props.isSmallScreen && (
          <button
            className="button"
            onClick={() => this.props.showSubpage(2)}
            style={{ margin: "10px auto 0 auto" }}
          >
            <div className="button-chevron reversed" />
            Powrt
          </button>
        )}
      </div>
    );
  }
}
